import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './guards/auth.guard';
import { from } from 'rxjs';

const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'home', loadChildren: './home/home.module#HomePageModule',canActivate:[AuthGuard]},
  { path: 'admin', loadChildren: './admin/admin.module#AdminPageModule',canActivate:[AuthGuard] },
  { path: 'login', loadChildren: './login/login.module#LoginPageModule' },
  { path: 'register', loadChildren: './register/register.module#RegisterPageModule' },
  { path: 'addquestion', loadChildren: './addquestion/addquestion.module#AddquestionPageModule' },
  { path: 'questions', loadChildren: './questions/questions.module#QuestionsPageModule',canActivate:[AuthGuard]},
  { path: 'users', loadChildren: './users/users.module#UsersPageModule' },
  { path: 'userprofile', loadChildren: './userprofile/userprofile.module#UserprofilePageModule' },
  { path: 'questionsadmin', loadChildren: './questionsadmin/questionsadmin.module#QuestionsadminPageModule' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
